// hin th danh sch tt c trang phim

import React, { useContext, useState } from "react";

import { ApiContext } from "../../../store/data_movies";
import { useTrailer } from "../../../store/data_movies_trailer";

import MovieDetail from "./MovieDetail";

import MovieListItems from "./MovieListItems";
import styles from "./MovieList.module.css";

const MovieList = () => {
  const [data_movies, setData_movies] = useState(null);
  const [overview, setOverview] = useState(null);
  const [fetch_name, setFetch_name] = useState(null);
  const [Id, setId] = useState(null);

  const [isShow, setIsShow] = useState(false);

  // ly d liu trailer t api

  const { key_Trailer, Trailer } = useTrailer(isShow, Id);

  const { requests } = useContext(ApiContext);

  const list_movide = {
    Original: "fetchNetflixOriginals",
    "Xu hng": "fetchTrending",
    "Xp hng cao": "fetchTopRated",
    "Hnh ng": "fetchActionMovies",
    Hi: "fetchComedyMovies",
    "Kinh d": "fetchHorrorMovies",
    "Lng mn": "fetchRomanceMovies",
    "Ti liu": "fetchDocumentaries",
  };
  const khi_nhan_vao_anh = (value, fetch, overview, id) => {
    if (Id === null) {
      // Click vo nh ln u
      setData_movies(value);
      setId(id);
      setFetch_name(fetch);
      setOverview(overview);
      setIsShow(true);
    } else if (Id === id) {
      // Click li vo nh ca cng mt b phim
      setIsShow(!isShow); // n/hin th thng tin b phim
    } else if (Id !== id) {
      // Click vo nh mt b phim khc
      setData_movies(value);
      setId(id);
      setFetch_name(fetch);
      setOverview(overview);
      setIsShow(true);
    }
  };

  const Content = () => {
    return Object.keys(list_movide).map((key) => {
      var key_requests = list_movide[key];
      return (
        <div key={key_requests}>
          <h1>{key}</h1>
          <div className={styles.list}>
            <MovieListItems
              onChange={khi_nhan_vao_anh}
              data={requests[key_requests]}
              fetch={key_requests}
              mode={"all"}
            />
          </div>
          {isShow === true && fetch_name === key_requests && (
            <MovieDetail
              value={data_movies}
              overview={overview}
              id={key_Trailer}
              Trailer={Trailer}
            />
          )}
        </div>
      );
    });
  };

  return <div className={styles.MovieList}>{Content()}</div>;
};

export default MovieList;
